.App {
    font-family: "Courier New", Courier, monospace;
    text-align: center;
    min-width: fit-content;
    max-width: 500px;
    margin: auto;
    background: rgb(45, 45, 45);
    background: linear-gradient(315deg, rgba(45, 45, 45, 1) 14%, rgba(52, 52, 52, 1) 97%);
    color: rgb(255, 255, 255);
    padding: 2em;
    border-radius: 2em;
    box-shadow:
        0 4px 8px 0 rgba(0, 0, 0, 0.2),
        0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

h1 {
    font-weight: 700;
    text-shadow: 1px 1px 5px rgb(0, 0, 0);
}

input {
    width: auto;
    padding: 0.6em;
    font-size: large;
    border-radius: 0.9em;
    border: 3px solid #927d9b;
    background-color: white;
    color: black;
    font-weight: 700;
    box-shadow:
        0 4px 8px 0 rgba(0, 0, 0, 0.2),
        0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

#question {
    font-size: 2.4em;
    font-weight: 700;
    color: rgb(233, 233, 233);
    border: 4px solid rgb(51, 51, 51);
    border-radius: 0.5em;
    padding: 0.7em;
    width: 70%;
    min-width: 100px;
    height: 50px;
    margin: 0.8em auto;
    background: rgb(27, 27, 27);
    text-shadow: 1px 1px 5px rgb(0, 0, 0);
}

#score {
    font-size: 1.1em;
    font-weight: 600;
    text-shadow: 1px 1px 5px rgb(0, 0, 0);
    background-color: #383838;
    margin: 0 20%;
    padding: 8px 10px;
    border-radius: 15px;
    box-shadow:
        0 4px 8px 0 rgba(12, 12, 12, 0.2),
        0 6px 20px 0 rgba(116, 116, 116, 0.1);
}

#timer {
    font-size: 1.1em;
    font-weight: 600;
    text-shadow: 1px 1px 5px rgb(0, 0, 0);
    background-color: #383838;
    margin: 5px 20%;
    padding: 8px 10px;
    border-radius: 15px;
    box-shadow:
        0 4px 8px 0 rgba(12, 12, 12, 0.2),
        0 6px 20px 0 rgba(116, 116, 116, 0.1);
}

#settingsBar {
    text-align: right;
}

#settingsBar button {
    background: none;
    border: none;
    color: white;
    cursor: pointer;
}

#settings {
    border-radius: 20px;
    margin-top: 5px;
    margin-bottom: 25px;
    min-width: inherit;
    max-width: 500px;
}

.input-correct-anim {
    animation:
        glow 0.2s ease-out 0s 2,
        shake 0.2s ease-in-out 0s 2;
}

.input-correct-no-anim {
    animation: glow ease-out 0.4s;
}

.new-question-anim {
    animation: growText 0.5s ease-in;
}

.score-correct-anim {
    animation: grow 0.3s ease-in;
    font-weight: 600;
}

.toggle-box {
    display: block;
    text-align: left;
    margin: 5px auto;
    padding: 0.6em;
    border: 1px solid rgb(56, 56, 56);
    border-radius: 15px;
    background-color: #383838;
    cursor: pointer;
    width: 220px;
}

.toggle-box label {
    font-weight: 600;
}

.toggle-box input {
    margin-right: 10px;
    padding: 5px;
}

.hidden {
    display: none;
    visibility: hidden;
}

.link {
    font-weight: 600;
    cursor: pointer;
}

#score-msg {
    font-weight: 700;
    font-size: large;
    margin: 20px;
    text-shadow: 1px 1px 5px rgb(0, 0, 0);
}

#score-board {
    padding: 10px;
    background-color: #242424;
    border-radius: 20px;
    margin: 10px auto;
    max-width: 400px;
}

.score-list-item {
    background-color: #383838;
    border-radius: 15px;
    padding: 7px;
    margin-top: 8px;
    color: rgb(204, 204, 204);
    font-size: small;
}

#privacy,
#mode-intro {
    font-size: smaller;
    border-radius: 16px;
    margin: 10px auto;
    width: auto;
    max-width: 500px;
}

#privacy p {
    padding: 0 15px;
}


.privacy-btn {
    font-family: inherit;
    font-weight: 700;
    margin: 10px 0;
    padding: 7px;
    cursor: pointer;
    border-radius: 20px;
    border: 4px solid #383838
}

#deleted-msg {
    background-color: #25a550;
    font-weight: 700;
    color: azure;
    padding: 12px;
    border-radius: 15px;
    margin-bottom: 15px;
}

.start-link {
    cursor: pointer;
}

@keyframes shake {
    0% {
        margin-left: 0rem;
    }

    25% {
        margin-left: 0.2rem;
    }

    75% {
        margin-left: -0.2rem;
    }

    100% {
        margin-left: 0rem;
    }
}

@keyframes glow {
    0% {
        box-shadow: 0em 0em 1em rgb(107, 196, 111);
    }

    25% {
        box-shadow: 0em 0em 1em rgb(107, 196, 111);
    }

    75% {
        box-shadow: 0em 0em 1em rgb(107, 196, 111);
    }

    100% {
        box-shadow: 0 0 0 rgb(238, 173, 235);
    }
}

@keyframes growText {
    from {
        transform: scale(0.4);
        border-color: rgb(1, 143, 1);
    }

    to {
        transform: scale(1);
        border-color: rgb(51, 51, 51);
    }
}

@keyframes grow {
    0% {
        font-size: 0.3em;
    }

    25% {
        font-size: 0.5em;
    }

    75% {
        font-size: 0.8em;
    }

    100% {
        font-size: 1.1em;
    }
}